// common mixins - start

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
    @include eltdfRelativeHolderLayout(top);
    margin: 13px 0 0;
    clear: both;

    ul {
        @include eltdfRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 8px;

            a {
                position: relative;
                display: flex;
                min-width: 24px;
                min-height: 24px;
                margin: 0;
                align-items: center;
                justify-content: center;
                padding: 8px 9px 5px;
                z-index: 2;
                box-sizing: content-box;

                &:hover {
                    color: $default-text-color;

                    + .eltdf-svg-border {
                        stroke-dashoffset: 0;
                        stroke-dasharray: 135;
                        transition: all .7s ease;
                    }
                }
            }

            .eltdf-svg-border {
                @include eltdfAbsoluteHolderLayout();
                z-index: 1;
                stroke-dashoffset: 135;
                stroke-dasharray: 135;
                stroke-linejoin: round;
                stroke-miterlimit: 10;
                stroke-linecap: butt;
                transform: rotate(-90deg);
                transform-origin: center;
                transition: all .7s ease;

                circle {
                    stroke-width: 1.4;
                }
            }

            &.eltdf-pag-active {
                
                .eltdf-svg-border {
                    stroke-dashoffset: 0;
                    stroke-dasharray: 135;
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
                margin: 0 2px;

                a {
                    padding: 9px;
                    border: none;

                    svg {

                        > * {
                            stroke: #fff;
                            transition: all .2s ease-in-out;
                        }
                    }

                    &:hover {

                        svg {

                            > * {
                                stroke: $first-main-color;
                            }
                        }
                    }
                }

                .eltdf-svg-border {
                    display: none;
                }
            }
	
	        &.eltdf-pag-prev,
	        &.eltdf-pag-first {
		        transform: scaleX(-1);
	        }

            &.eltdf-pag-first,
            &.eltdf-pag-last {
                display: none;
            }

            @if ($list_type == 'shortcode') {
                
                &.eltdf-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.eltdf-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {

                span {
                    position: relative;
                    display: flex;
                    min-width: 24px;
                    min-height: 24px;
                    margin: 0;
                    align-items: center;
                    justify-content: center;
                    padding: 8px 9px 5px;

                    &.current {

                        + .eltdf-svg-border {
                            stroke-dashoffset: 0;
                            stroke-dasharray: 135;
                        }
                    }
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;
                        padding: 10px 9px;
                        border: none;

                        &:before {
                            content: '';
                            display: block;
                            width: 10px;
                            height: 11px;
                            background-image: url('../../assets/svg/dd-arrows.svg');
                            background-repeat: no-repeat;
                            transition: background-image .2s ease-in-out;
                        }

                        &:hover {

                            &:before {
                                background-image: url('../../assets/svg/dd-arrows-hover.svg');
                            }
                        }

                        + .eltdf-svg-border {
                            display: none;
                        }
                    }

                    &.prev {
                        margin-right: -10px;
	                    transform: scaleX(-1);
                    }

                    &.next {
                        margin-left: -10px;
                    }
                }
            }
        }
    }
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $additional-text-font;
    font-size: 14px;
    line-height: 2em;
    font-weight: 300;
    letter-spacing: .36em;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin eltdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 7px 35px;

    } @else if ($size == 'medium') {
        padding: 9px 39px;

    } @else if ($size == 'large') {
        padding: 11px 50px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin eltdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid $first-main-color $important;
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: $default-text-color $important;
    background-color: transparent $important;
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $default-text-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
    color: $default-text-color $important;
    background-color: transparent $important;
    border-color: $first-main-color $important;
}

@mixin eltdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

@mixin eltdfSimpleButtonAnimationDefault($left:0, $bottom:0, $width:100%, $height:3px) {

    .eltdf-btn-first-line,
    .eltdf-btn-second-line {
        content: '';
        position: absolute;
        bottom: $bottom;
        left: $left;
        right: auto;
        height: $height;
        background-color: transparent;
        border-top: 1px solid $first-main-color;
        width: $width;
        transform: scale(0,1);
        transform-origin: 0% 50%;
        transition: transform .2s;
    }

    .eltdf-btn-second-line {
        transform-origin: 100% 50%;
        transform: scale(1,1);
    }

    &:before,
    &:after {
        content: '';
        position: absolute;
        bottom: -1px;
        left: $left;
        right: auto;
        height: $height;
        background-color: transparent;
        border-bottom: 1px solid $first-main-color;
        width: $width;
        transform: scale(0,1);
        transform-origin: 0% 50%;
        transition: transform .2s;
    }

    &:after {
        transform-origin: 100% 50%;
        transform: scale(1,1);
    }
}

@mixin eltdfSimpleButtonAnimationHover($left:0, $bottom:0, $width:100%, $height:3px) {

    .eltdf-btn-first-line {
        transform: scale(1,1);
        transition: transform .5s cubic-bezier(0.48, 0.57, 0.33, 0.89) .2s;
    }

    .eltdf-btn-second-line {
        transform: scale(0,1);
        transition: transform .5s cubic-bezier(0.48, 0.57, 0.33, 0.89);
    }

    &:before {
        transform: scale(1,1);
        transition: transform .7s cubic-bezier(0.48, 0.57, 0.33, 0.89) .3s;
    }

    &:after {
        transform: scale(0,1);
        transition: transform .7s cubic-bezier(0.48, 0.57, 0.33, 0.89);
    }
}

@mixin eltdfOutlineButtonAnimation() {
    overflow: hidden;

    .eltdf-btn-before-line,
    .eltdf-btn-after-line {
        width: 1px;
        display: inline-block;
        background-color: $first-main-color;
        position: absolute;
        top: 0;
        left: 0;
        transform: rotate(72deg) scale(1, 0);
        transform-origin: top;
        transition: transform .4s cubic-bezier(0.63, 0.25, 0.25, 1);
        will-change: transform;
    }

    .eltdf-btn-after-line {
        transform-origin: bottom;
        top: -2px;
    }

    &:hover {

        .eltdf-btn-before-line,
        .eltdf-btn-after-line {
            transform: rotate(72deg) scale(1, 1);
        }
    }
}

@mixin eltdfSpecialButtonAnimation() {

    svg {
        position: absolute;
        bottom: 0;
        width: auto;
        height: 100%;

        polyline {

            &:nth-child(3),
            &:nth-child(4) {
                stroke-dashoffset: 57;
                stroke-dasharray: 57;
            }
        }

        &.eltdf-sb-left {
            left: 0;
        }

        &.eltdf-sb-right {
            right: 0;
            transform: rotate(180deg);
        }
    }

    &.eltdf-btn-animation-in {

        svg {

            polyline {

                &:nth-child(3),
                &:nth-child(4) {
                    animation: in 1s forwards 0s;
                }
            }

            @keyframes in {
                0% {
                    stroke-dashoffset: 57;
                }
                100% {
                    stroke-dashoffset: 0;
                }
            }
        }
    }

    &.eltdf-btn-animation-out {

        svg {

            polyline {

                &:nth-child(3),
                &:nth-child(4) {
                    animation: out 1s backwards 0s;
                }
            }

            @keyframes out {
                100% {
                    stroke-dashoffset: 57;
                }
                0% {
                    stroke-dashoffset: 0;
                }
            }
        }
    }
}

@mixin eltdfLinesAnimationDefault() {
    position: relative;
    display: inline-block;

    &:before,
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid $first-main-color;
        transform: scale(0,1);
        transform-origin: 0 50%;
        transition: transform .4s cubic-bezier(.28,.38,0,.81);
    }

    &:before {
        padding-bottom: 3px;
        transition: transform .6s cubic-bezier(.28,.38,0,.81);
    }

    &:hover {

        &:before,
        &:after {
            transform: scale(1,1);
        }
    }
}

@mixin eltdfLinesAnimationHover() {
    position: relative;
    display: inline-block;

    &:before,
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid $first-main-color;
        transform: scale(0,1);
        transform-origin: 0 50%;
        transition: transform .6s cubic-bezier(.28,.38,0,.81);
    }

    &:before {
        padding-bottom: 3px;
        transition: transform .4s cubic-bezier(.28,.38,0,.81);
    }

    &:hover {

        &:before,
        &:after {
            transform: scale(1,1);
        }
    }
}

@mixin eltdfIconOverlay($width:auto, $height:auto, $offset:0) {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: $offset;
    top: $offset;
	right:$offset;
	bottom:$offset;
    background-color: $additional-background-color;
    opacity: 0;
    transition: opacity .2s ease-in-out;

    img {
        width: auto;
        height: auto;
    }

    svg {
        width: $width;
        height: $height;
    }
}

@mixin eltdfPopoutSVGArrows() {

	svg {
		max-height: 100%;
		width: auto;
		stroke-width: 1px;
		transform: translateX(0);
		
		.eltdf-popout {
            stroke-dashoffset: 43;
            stroke-dasharray: 43;
            stroke-linejoin: round;
            stroke-miterlimit: 10;
            stroke-linecap: butt;
            transform: rotateX(180deg);
            transform-origin: center center;
            transition: stroke-dashoffset .5s cubic-bezier(0.48, 0.57, 0.33, 0.89);
		}
	}
}

@mixin eltdfPopoutSVGArrowsHover() {

    svg {

        .eltdf-popout {
            transform: translateX(0) rotateX(180deg);
            stroke-dashoffset: 0;
            stroke-dasharray: 43;
            transition: stroke-dashoffset .5s cubic-bezier(0.48, 0.57, 0.33, 0.89);
        }
    }
}

// common mixins - end

