// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px #{$input-vertical-padding - 4}px;
    font-family: inherit;
    font-size: 19px;
    font-weight: inherit;
    line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    @include eltdfPlaceholder {
        color: inherit;
    }
}

