// heading mixins - start

@mixin eltdfH1() {
    font-size: 48px;
    line-height: 1.27em;
    letter-spacing: .22em;
}

@mixin eltdfH2() {
    font-size: 41px;
    line-height: 1.31em;
    letter-spacing: .23em;
}

@mixin eltdfH3() {
    font-size: 36px;
    line-height: 1.30em;
    letter-spacing: .23em;
}

@mixin eltdfH4() {
    font-size: 28px;
    line-height: 1.39em;
    letter-spacing: .23em;
}

@mixin eltdfH5() {
    font-size: 19px;
    line-height: 1.52em;
    letter-spacing: .23em;
}

@mixin eltdfH6() {
    font-size: 17px;
    line-height: 1.58em;
    letter-spacing: .22em;
}

@mixin eltdfDefaultHeadingStyle($margin: true) {
    font-family: $default-heading-font;
    font-weight: 700;
    text-transform: uppercase;
    color: $first-main-color;

    @if ($margin) {
        margin: 25px 0;
    }

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include eltdfTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin eltdfHeadingStyle($with-heading: null, $with-color: true, $margins: true) {

    @include eltdfDefaultHeadingStyle($margins);

    @if ($with-color) {
        color: $default-heading-color;
    }

    @if ($with-heading == 'h1') {
        @include eltdfH1();
    } @else if ($with-heading == 'h2') {
        @include eltdfH2();
    } @else if ($with-heading == 'h3') {
        @include eltdfH3();
    } @else if ($with-heading == 'h4') {
        @include eltdfH4();
    } @else if ($with-heading == 'h5') {
        @include eltdfH5();
    } @else if ($with-heading == 'h6') {
        @include eltdfH6();
    }
}

@mixin eltdfDefaultScriptStyle() {
    font-family: $default-script-font;
    font-size: 19px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: .04em;
    color: $default-script-color;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
}

@mixin eltdfBody() {
    font-family: $default-text-font;
    font-size: 19px;
    font-weight: 300;
    line-height: 29px;
    color: $default-text-color;
    letter-spacing: 0;
    text-transform: none;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

